@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300&display=swap);
html, body, #root {
  border: 0;
  margin: 0;
  padding: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100vh;
  overflow: hidden;
}

.react-tabs__tab-list {
  background-color: rgba(233, 233, 233, 0.22);
}

* {
  box-sizing: border-box;
}

.react-tabs__tab-panel {
  height: 100%;
}
.App {
  font-family: 'Roboto Slab', serif;
  height: 100vh;
}

.appHeader {
  padding-left: 10px;
  position: absolute;
  right: 0;
  padding: 0;
  margin: 6px;
}

.appTabs {
  height: 100vh;
  position: relative;
}

.react-tabs {
    -webkit-tap-highlight-color: transparent;
}

.react-tabs__tab-list {
    border-bottom: 1px solid #aaa;
    margin: 0 0 1px;
    padding: 0;
    height: 2.09em;
}

.react-tabs__tab {
    display: inline-block;
    border: 1px solid transparent;
    border-bottom: none;
    bottom: -1px;
    position: relative;
    list-style: none;
    padding: 6px 12px;
    cursor: pointer;
    margin-left: 10px;
}

.react-tabs__tab--selected {
    background: #fff;
    border-color: #aaa;
    color: black;
    border-radius: 5px 5px 0 0;
    margin-top: 2px;
}

.react-tabs__tab--disabled {
    color: GrayText;
    cursor: default;
}

.react-tabs__tab:focus {
    box-shadow: 0 0 5px hsl(208, 99%, 50%);
    border-color: hsl(208, 99%, 50%);
    outline: none;
}

.react-tabs__tab:focus:after {
    content: "";
    position: absolute;
    height: 5px;
    left: -4px;
    right: -4px;
    bottom: -5px;
    background: #fff;
}

.react-tabs__tab-panel {
    display: none;
}

.react-tabs__tab-panel--selected {
    display: block;
    background: #fff;
    height: calc(100% - 2.09em);
}

.ratingBody {
    height: calc(100% - 40px); /* sorry for the hardcoding not sure why it doesn't pick up the height right*/
    width: 100%;
    position: absolute;
    display: flex;
    flex-direction: column;
}

textarea {
    width: 100%;
    line-height: 1.02rem;
    height: 400px;
    padding: 10px;
}

.modified > .accordion__button {
    background-color: #bbf1bb;
}

section.rating {
    text-align: left;
    margin: 30px;
}

.ratingScoresAndComments {
    width: 100%;
    border-top: 1px solid #aaa;
    position: relative;
    flex: 1 1;
}

.ratingComments {
    position: absolute;
    top: 0;
    left: 60%;
    bottom: 0px;
    padding-right: 6px;
    padding-left: 6px;
    width: 40%;
    overflow-y: auto;
}

.ratingScores {
    position: absolute;
    top: 0px;
    width: 60%;
    left: 0;
    bottom: 0px;
    overflow-y: scroll;
}

.ratingFooter {
    flex: 0 1;
    border-top: 1px solid grey;
    background-color: white;
    text-align: right;
    padding: 6px;
}

.ratingFooter button {
    font-size: x-large;
    border-radius: 4px;
}

.ratingFooter button:hover {
    background-color: black;
    color: white;
}

.ratingDescription {
    background-color: #969595;
    color: white;
    padding: 6px;
    flex: 0 1;
}

.outOfRangeMessage {
    font-size: x-small;
}
.RatingSlider {
    padding: 0px 20px 80px;
}

.refPoint {
    border: 1px solid #dddd00;
    max-width: 110px;
    border-radius: 6px;
    padding: 2px;
    background-color: #fffff2;
    font-size: x-small;
}

.rc-slider-mark-text {
    z-index: 1;
}

.rc-slider-mark-text:hover {
    z-index: 2;
}

.Rating {
    position: relative;
    padding: 0;
    --line-color: #e8e6ee;
    --line-width: 1.5px;
    outline: none;
}

.Rating.enabled:focus {
    background-color: rgba(0, 0, 0, 0.015);
}

.Rating.disabled {
    filter: saturate(0.4)
}

.Rating .label {
    font-family: "Roboto Slab", serif;
    padding: 2px;
    border: 1px solid #ddd;
    background-color: #eee;
    z-index: 10;
    font-size: x-small;
    border-radius: 3px;
    -webkit-user-select: none;
            user-select: none;
    overflow: hidden;
}

.Rating.enabled .label:hover {
    z-index: 20;
    filter: saturate(2.2);
}

.Rating .line {
    fill: transparent;
    stroke: var(--line-color);
    stroke-width: var(--line-width);
}

.Rating .point {
    fill: white;
    r: 4px;
    stroke: var(--line-color);
    stroke-width: var(--line-width);
}

/* track */
.Rating .track {
    stroke-width: 6;
    stroke-linecap: round;
    stroke: #e9e9e9;
    pointer-events: none;
}

.Rating .track.selected {
    stroke-width: 6;
    stroke: #afe3fc;
    background-color: #afe3fc;
}

/* The 'div' is just to increase the specificity */

.Rating div .selected {
    stroke-width: 3;
    stroke: #afe3fc;
    background-color: #afe3fc;
    border: 1px solid #45a9c6;
    r: 6px;
}

/*  Data set colours */

.Rating .anchor {
    background-color: #fff8c1;
    border: 1px solid #eee663;
    stroke: #eee663;
}

.Rating .previousRatings {
    background-color: #ffaeb7;
    border: 1px solid #cd0c21;
    stroke: #cd0c21;
}

.NewRatingPanel, ratingView {
    height: 100%;
    margin: 0;
    padding: 0;
    position: relative;
}

.ratingSelect {
    padding: 6px;
    height: 2.2em;
}

.newRatingDisplay {
    height: calc(100% - 2.2em);
}
.RatingsTable {
    width: 100%;
    font-size: small;
    -webkit-user-select: none;
            user-select: none;
    overflow-y: scroll;
    overflow-x: hidden;
    height: 200px;
    border-bottom: 1px solid grey;
}

.RatingsTable.nonSelected {
    height: 100%;
}

.boolCol {
    text-align: center;
}

.RatingsTable table {
    width: 100%;
    border-collapse: collapse;
}

.RatingsTable thead {
    background-color: black;
    color: white;
}

.accordion {
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 2px;
}

.accordion__item + .accordion__item {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}

.accordion__button {
    background-color: #f4f4f4;
    color: #444;
    cursor: pointer;
    padding: 18px;
    width: 100%;
    text-align: left;
    border: none;
}

.accordion__button:hover {
    background-color: #ddd;
}

.accordion__button:before {
    display: inline-block;
    content: '';
    height: 10px;
    width: 10px;
    margin-right: 12px;
    border-bottom: 2px solid currentColor;
    border-right: 2px solid currentColor;
    transform: rotate(-45deg);
}

.accordion__button[aria-expanded='true']::before,
.accordion__button[aria-selected='true']::before {
    transform: rotate(45deg);
}

[hidden] {
    display: none;
}

.accordion__panel {
    padding: 20px;
    padding-top: 0px;
    animation: fadein 0.35s ease-in;
}

/* -------------------------------------------------- */
/* ---------------- Animation part ------------------ */
/* -------------------------------------------------- */

@keyframes fadein {
    0% {
        opacity: 0;
    }

    100% {
        opacity: 1;
    }
}

.ratingView {
    height: 100%;
}


.TeamRate .ratingView {
    height: calc(100% - 180px);
    position: relative;
}

.TeamRate .RatingsTable {
    width: 100%;
    font-size: small;
    -webkit-user-select: none;
            user-select: none;
    overflow-y: scroll;
    overflow-x: hidden;
    height: 180px;
    border-bottom: 1px solid grey;
    padding: 6px;
}

.RatingsTable td {
    text-align: center;
}

.TeamRate {
    height: 100%;
}

.TeamRate .teammate.unrated {
    background-color: #ffe3e8;
}

.TeamRate .ratingView p {
    margin: 6px;
}

.AxisPanel .AxisEditor {
    height: calc(100% - 180px);
    overflow-y: scroll;
}

.AxisPanel .AxisTable {
    width: 100%;
    font-size: small;
    -webkit-user-select: none;
            user-select: none;
    overflow-y: scroll;
    overflow-x: hidden;
    height: 180px;
    border-bottom: 1px solid grey;
    padding: 6px;
}

.AxisPanel {
    height: 100%;
}

.AxisTable.noSelection {
    height: 100%;
}
.AxisEditor {
    padding: 6px;
}

.AxisEditor label {
    width: 120px;
    display: inline-block;
}

.AxisEditor input[type=text] {
    width: 550px;
}

.AxisEditor .RatingSlider {
    margin-bottom: 50px;
}

.axisDescription {
    font-size: small;
}

.AxisEditor span.group {
    font-family: monospace;
}

.AxisEditor .audienceSetting {
    padding-left: 40px;
}

.AxisEditor .submitSection {
    border-top: 1px solid black;
    padding: 10px;
    margin-top: 20px;
}
